@import url("./colors.css");

#board {
  width: 75%;
  display: grid;
  /* grid-template-columns: 1fr 3fr 6fr; */
  grid-gap: 1vh;
  font-family: arial;
  font-size: 1.75vh;
}

#board > div {
  border: 1px solid #cccccc;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 0.5vh;
  padding: 0.5vh;
}

#trains {
  grid-column: 1 / 4;
}

#job-market {
  grid-row: 2;
  grid-column: 1;
}

#cow-market {
  grid-column: 1/ 3;
  grid-row: 3;
}

#board-players {
  grid-column: 3;
}

#board-info {
  grid-row: 3;
  grid-column: 3;
}

.current-player-0 {
  background-color: var(--player0-color);
}

.current-player-1 {
  background-color: var(--player1-color);
}
.current-player-2 {
  background-color: var(--player2-color);
}
.current-player-3 {
  background-color: var(--player3-color);
}
